ades del producto, haciendo que los usuarios que lo aprueben se inscriban a través de un formulario. Este experimento permite comprobar que el producto tiene posibilidades en el mercado real.")]),_c('p',[_c('strong',[_vm._v("Airbnb: ")]),_vm._v("en al año 2007, los dueños de este emprendimiento se dieron cuenta que en cierto tiempo del año todos los hoteles en San Francisco - Estados Unidos, estaban con la ocupación de sus habitaciones al 100% y muchas personas resultaban sin hospedaje. La hipótesis de los emprendedores fue que algunas personas pagarían por albergarse en la casa de una persona en particular. Como resultado, crearon una página web con una lista de apartamentos, y así poder validar si alguien estaba interesado. Luego de lograr la reserva de 3 huéspedes, validaron su idea.")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-4",attrs:{"titulo":"Video explicativo"}},[_c('h4',[_vm._v("Video explicativo")]),_c('p',[_vm._v("Esta prueba consiste en crear un video de no más de 30 minutos, en el que se explique lo que hace el producto y por qué las personas deberían comprarlo, es decir, explicar su beneficio. Generalmente, la prueba se realiza en situaciones en las que resulta muy difícil explicar el producto con palabras y su desarrollo es de alto costo. Por ejemplo, un programa de "),_c('i',[_vm._v("software")]),_vm._v(" donde se requiera mostrar, a través de un video, cómo funciona, los pasos, la velocidad u otra característica tecnológica indispensable. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-4",attrs:{"titulo":"Técnica Mago de Oz"}},[_c('h4',[_vm._v("Técnica Mago de Oz")]),_c('p',{staticClass:"mb-4"},[_vm._v("Esta técnica permite validar la intención de un posible cliente de comprar, sin que este cuente con la logística para recibir el servicio. Se le conoce a esta técnica "),_c('strong',[_vm._v("“Mago de Oz”, ")]),_vm._v("por el personaje del mago en la película del mismo nombre, en la cual el personaje del mago crea una ilusión de un producto real, haciendo parecer que el producto funciona completamente y es totalmente automatizado desde afuera, pero las cosas son maniobradas por personas, es decir, la operación se hace manualmente. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Lo que se busca es ofrecer una promesa de valor y probar una suposición con el mínimo uso de la tecnología. ")]),_c('p',{staticClass:"mb-4"},[_c('strong',[_vm._v("Zappos: ")]),_vm._v("la idea del creador de este emprendimiento era vender zapatos en línea. Quería probar la siguiente hipótesis inicial: si era cierto que había público que quisiera pagar por comprar zapatos en línea. ")]),_c('p',[_vm._v("Realizó el siguiente experimento: habló con el propietario de una zapatería de su barrio, les tomó fotos a unos zapatos de su local y las publicó en su página web para venderlos. Cuando le realizaban un pedido, el fundador iba al local de zapatos donde tomaba las fotos y compraba los zapatos, los empacaba como si fueran suyos y los enviaba al cliente. Finalmente, este emprendimiento tuvo éxito, porque fue comprado por Amazon en el año 2009 por $1.200 millones de dólares. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-4",attrs:{"titulo":"Conserjería"}},[_c('h4',[_vm._v("Conserjería")]),_c('p',{staticClass:"mb-4"},[_vm._v("Esta técnica tiene que ver con el aprendizaje que obtenemos de los primeros clientes del producto, ya que en un principio no se cuenta con todo el soporte para atender a un gran número de clientes, es decir, se hace todo el trabajo para los primeros. Se usa cuando se requiere entender el comportamiento del cliente y lo que se debe hacer para solucionar su problema.")]),_c('p',{staticClass:"mb-4"},[_c('strong',[_vm._v("Ejemplo: ")]),_vm._v("Una página web que ayuda a las personas a realizar su compra de alimentos saludables de forma semanal. El objetivo de este emprendimiento es ayudar a diseñar un plan de alimentación saludable por semana, en el cual se hace una lista de la compra ayudándo